<template>
  <div class="container">
    <div class="grid">
      <div 
        v-for="l in listObj"
        :key="l.name"
        class="poke-card"
      >
        <span>#{{ l.id }}</span>
        <img :src="l.sprites.other.dream_world.front_default" alt="">
        <h2 
          :class="l.types[0].type.name"
        >
        {{ firstUpperCase(l.name) }}</h2>
      </div>
    </div>
    <button @click="addPokemonsToList">Cargar más Pokemones</button>
  </div>
</template>

<script>
import api from '@/api'
export default {
  name: 'PmList',
  data() {
    return {
      listObj: [],
    }
  },
  created() {
    this.getPokemonList()
  },
  computed: {
    listEndIndex() {
      return (this.listObj.length + 1)
    }
  },
  methods: {
    async getPokemonList() {
      for(let i = 1; i <= 20; i ++) {
        const data = await api.getPokemon(i)
        this.listObj.push(data)
      }
    },
    firstUpperCase(name) {
      const newName = name[0].toUpperCase() + name.slice(1)
      return newName
    },
    async addPokemonsToList() {
      const endIndex = this.listEndIndex
      for(let i = endIndex; i < endIndex + 20; i++) {
        const data = await api.getPokemon(i)
        this.listObj.push(data)
      }
    }
  }
}
</script>
  
