<template>
  <section>
    <div class="search-container">
      <div class="input-container">
        <input v-model="inputPkm" type="text" />
        <button v-on:click="searchPkm">
          <img src="@/assets/search.svg" alt="" />
        </button>
      </div>
    </div>
    <div class="cards-container">
      <button
        v-if="showArrow"
        @click="previousPokemon"
        class="navigate-btn-back"
      >
        <span></span>
        <span></span>
      </button>
      <pm-card v-if="showCard" :dataObj="dataObj" />
      <button
        v-if="showArrow"
        @click="nextPokemon"
        class="navigate-btn-forward"
      >
        <span></span>
        <span></span>
      </button>
    </div>
  </section>
</template>

<script>
import api from "@/api";
import PmCard from "@/components/PmCard";
export default {
  name: "PmSelect",
  components: { PmCard },
  data() {
    return {
      inputPkm: "",
      dataObj: {},
      showCard: false,
      showArrow: false,
    };
  }, 
  methods: {
    previousPokemon() {
      if (this.dataObj.id <= 1) {
        return;
      } else {
        api.getPokemon(this.dataObj.id - 1).then((data) => {
          this.dataObj = data;
        });
      }
    },
    async nextPokemon() {
      const data = await api.getPokemon(this.dataObj.id + 1)
      this.dataObj = data
    },
    async searchPkm() {
      const data = await api.getPokemon(this.inputPkmLower)
      this.dataObj = data
      this.showCard = true
      this.showArrow = true
    },
  },
  computed: {
    inputPkmLower() {
      return this.inputPkm.toLowerCase()
    }
  }
};
</script>

