<template>
  <div v-if="showCard" class="container">
    <div class="title-container">
      <h2>{{ firstToUpperCase }}</h2>
      <span>#{{ id }}</span>
    </div>
    <div class="type-container">
      <span v-for="t in types" :key="t.type.name">
        <p :class="t.type.name">{{ t.type.name }}</p>
      </span>
    </div>
    <figure>
      <img :src="img" :alt="name" />
    </figure>
    <section class="wh-container">
      <span><strong>weight: </strong>{{ kg }}kg</span>
      <span><strong>Height: </strong>{{ mts }}mts</span>
    </section>
    <ul class="stats-container">
      <li class="stats-hp">{{ hp }}<br />Hp</li>
      <li class="stats-atk">{{ atk }}<br />Atk</li>
      <li class="stats-def">{{ def }}<br />Def</li>
      <li class="stats-sp-atk">{{ spAtk }}<br />Sp.Atk</li>
      <li class="stats-sp-def">{{ spDef }}<br />Sp. Def</li>
      <li class="stats-speed">{{ speed }}<br />Speed</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "PmCard",
  props: {
    dataObj: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      name: "",
      id: "",
      types: this.dataObj.types,
      img: "",
      weight: 0,
      height: 0,
      hp: 0,
      atk: 0,
      def: 0,
      spAtk: 0,
      spDef: 0,
      speed: 0,
      showCard: false
    };
  },
  computed: {
    kg() {
      return parseInt((this.weight * 100) / 1000);
    },
    mts() {
      return parseFloat(this.height / 10).toFixed(1);
    },
    firstToUpperCase() {
      return this.dataObj.name[0].toUpperCase() + this.dataObj.name.slice(1)
    }
  },
  methods: {
    getPokemonData() {
      console.log("hola");
    },
  },
  mounted() {
    // let name = this.dataObj.name;
    // name = name[0].toUpperCase() + name.slice(1);
    this.name = this.dataObj.name;
    this.id = this.dataObj.id;
    this.types = this.dataObj.types;
    this.img = this.dataObj.sprites.other.dream_world.front_default;
    this.weight = this.dataObj.weight;
    this.height = this.dataObj.height;
    this.hp = this.dataObj.stats[0].base_stat;
    this.atk = this.dataObj.stats[1].base_stat;
    this.def = this.dataObj.stats[2].base_stat;
    this.spAtk = this.dataObj.stats[3].base_stat;
    this.spDef = this.dataObj.stats[4].base_stat;
    this.speed = this.dataObj.stats[5].base_stat;
    this.showCard = true
  },
  watch: {
    dataObj() {
      this.img = this.dataObj.sprites.other.dream_world.front_default;
      let name = this.dataObj.name;
      name = name[0].toUpperCase() + name.slice(1);
      this.name = name;
      this.name = this.dataObj.name;
      this.id = this.dataObj.id;
      this.types = this.dataObj.types;
      this.weight = this.dataObj.weight;
      this.height = this.dataObj.height;
      this.hp = this.dataObj.stats[0].base_stat;
      this.atk = this.dataObj.stats[1].base_stat;
      this.def = this.dataObj.stats[2].base_stat;
      this.spAtk = this.dataObj.stats[3].base_stat;
      this.spDef = this.dataObj.stats[4].base_stat;
      this.speed = this.dataObj.stats[5].base_stat;

    },
  },
};
</script>

